
@import url(https://fonts.googleapis.com/css?family=Playfair+Display:400,900,700);
@import url(https://fonts.googleapis.com/css?family=Libre+Baskerville:400,400italic,700);
@import url(https://fonts.googleapis.com/css?family=Anonymous+Pro);
@import url(https://fonts.googleapis.com/css?family=Quicksand:400,700);
html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline
}
article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block
}
body{line-height:1;font-size:16px;-webkit-font-smoothing:antialiased;-moz-font-smoothing:antialiased;-o-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale
}
ol,ul{list-style:none;clear:both
}
blockquote,q{quotes:none
}
blockquote:before,blockquote:after,q:before,q:after{content:'';content:none
}
table{border-collapse:collapse;border-spacing:0
}
a{text-decoration:none
}
em{font-style:italic
}
strong{font-weight:bold
}
*,*:before,*:after{box-sizing:border-box
}
.nav,.pagination,.carousel,.panel-title a{cursor:pointer
}
.grey-bg{background-color:#f7f8fc
}
a{color:#b98a5b
}
@keyframes medium-editor-image-loading{
0%{-webkit-transform:scale(0);-webkit-transform-transform:scale(0)
}
100%{-webkit-transform:scale(1);-webkit-transform-transform:scale(1)
}
}
@keyframes medium-editor-pop-upwards{
0%{opacity:0;-webkit-transform:matrix(0.97, 0, 0, 1, 0, 12);-webkit-transform-transform:matrix(0.97, 0, 0, 1, 0, 12)
}
20%{opacity:.7;-webkit-transform:matrix(0.99, 0, 0, 1, 0, 2);-webkit-transform-transform:matrix(0.99, 0, 0, 1, 0, 2)
}
40%{opacity:1;-webkit-transform:matrix(1, 0, 0, 1, 0, -1);-webkit-transform-transform:matrix(1, 0, 0, 1, 0, -1)
}
100%{-webkit-transform:matrix(1, 0, 0, 1, 0, 0);-webkit-transform-transform:matrix(1, 0, 0, 1, 0, 0)
}
}
.medium-editor-anchor-preview{font-family:"Helvetica Neue", Helvetica, Arial, sans-serif;font-size:16px;left:0;line-height:1.4;max-width:280px;position:absolute;text-align:center;top:0;word-break:break-all;word-wrap:break-word;visibility:hidden;z-index:2000
}
.medium-editor-anchor-preview a{color:#fff;display:inline-block;margin:5px 5px 10px
}
.medium-editor-anchor-preview-active{visibility:visible
}
.medium-editor-dragover{background:#ddd
}
.medium-editor-image-loading{-webkit-animation:medium-editor-image-loading 1s infinite ease-in-out;-webkit-animation-animation:medium-editor-image-loading 1s infinite ease-in-out;background-color:#333;border-radius:100%;display:inline-block;height:40px;width:40px
}
.medium-editor-placeholder{position:relative
}
.medium-editor-placeholder::after{content:attr(data-placeholder) !important;font-style:italic;position:absolute;left:0;top:0;white-space:pre;padding:inherit;margin:inherit
}
.medium-editor-placeholder-relative{position:relative
}
.medium-editor-placeholder-relative::after{content:attr(data-placeholder) !important;font-style:italic;position:relative;white-space:pre;padding:inherit;margin:inherit
}
.medium-toolbar-arrow-under:after,.medium-toolbar-arrow-over:before{border-style:solid;content:'';display:block;height:0;left:50%;margin-left:-8px;position:absolute;width:0
}
.medium-toolbar-arrow-under::after{border-width:8px 8px 0 8px
}
.medium-toolbar-arrow-over::before{border-width:0 8px 8px 8px;top:-8px
}
.medium-editor-toolbar{font-family:"Helvetica Neue", Helvetica, Arial, sans-serif;font-size:16px;left:0;position:absolute;top:0;visibility:hidden;z-index:2000
}
.medium-editor-toolbar ul{margin:0;padding:0
}
.medium-editor-toolbar ul li{float:left;list-style:none;margin:0;padding:0
}
.medium-editor-toolbar ul li button{box-sizing:border-box;cursor:pointer;display:block;font-size:14px;line-height:1.33;margin:0;padding:15px;text-decoration:none
}
.medium-editor-toolbar ul li button::focus{outline:none
}
.medium-editor-toolbar ul li .medium-editor-action-underline{text-decoration:underline
}
.medium-editor-toolbar ul li .medium-editor-action-pre{font-family:Consolas, "Liberation Mono", Menlo, Courier, monospace;font-size:12px;font-weight:100;padding:15px 0
}
.medium-editor-toolbar-active{visibility:visible
}
.medium-editor-sticky-toolbar{position:fixed;top:1px
}
.medium-editor-relative-toolbar{position:relative
}
.medium-editor-toolbar-active.medium-editor-stalker-toolbar{-webkit-animation:medium-editor-pop-upwards 160ms forwards linear;-webkit-animation-animation:medium-editor-pop-upwards 160ms forwards linear
}
.medium-editor-action-bold{font-weight:bolder
}
.medium-editor-action-italic{font-style:italic
}
.medium-editor-toolbar-form{display:none
}
.medium-editor-toolbar-form input,.medium-editor-toolbar-form a{font-family:"Helvetica Neue", Helvetica, Arial, sans-serif
}
.medium-editor-toolbar-form .medium-editor-toolbar-form-row{line-height:14px;margin-left:5px;padding-bottom:5px
}
.medium-editor-toolbar-form .medium-editor-toolbar-input,.medium-editor-toolbar-form label{border:none;box-sizing:border-box;font-size:14px;margin:0;padding:6px;width:316px;display:inline-block
}
.medium-editor-toolbar-form .medium-editor-toolbar-input:focus,.medium-editor-toolbar-form label:focus{-webkit-appearance:none;-webkit-appearance--moz-appearance:none;-webkit-appearance--moz-appearance-appearance:none;border:none;box-shadow:none;outline:0
}
.medium-editor-toolbar-form a{display:inline-block;font-size:24px;font-weight:bolder;margin:0 10px;text-decoration:none
}
.medium-editor-toolbar-form-active{display:block
}
.medium-editor-toolbar-actions:after{clear:both;content:"";display:table
}
.medium-editor-element{word-wrap:break-word;min-height:30px
}
.medium-editor-element .medium-editor-element img{max-width:100%
}
.medium-editor-element .medium-editor-element sub{vertical-align:sub
}
.medium-editor-element .medium-editor-element sup{vertical-align:super
}
.medium-editor-hidden{display:none
}
.medium-toolbar-arrow-under:after{top:60px;border-color:#57ad68 transparent transparent transparent
}
.medium-toolbar-arrow-over:before{top:-8px;border-color:transparent transparent #57ad68 transparent
}
.medium-editor-toolbar{background-color:#57ad68
}
.medium-editor-toolbar li{padding:0
}
.medium-editor-toolbar li button{min-width:60px;height:60px;border:none;border-right:1px solid #9ccea6;background-color:transparent;color:#fff;-webkit-transition:background-color 0.2s ease-in,color 0.2s ease-in;-webkit-transition-transition:background-color .2s ease-in, color .2s ease-in
}
.medium-editor-toolbar li button::hover{background-color:#346a3f;color:#fff
}
.medium-editor-toolbar li .medium-editor-button-active{background-color:#23482a;color:#fff
}
.medium-editor-toolbar li .medium-editor-button-last{border-right:none
}
.medium-editor-toolbar-form .medium-editor-toolbar-input{height:60px;background:#57ad68;color:#fff
}
.medium-editor-toolbar-form .medium-editor-toolbar-input::-webkit-input-placeholder{color:#fff;color:rgba(255,255,255,0.8)
}
.medium-editor-toolbar-form .medium-editor-toolbar-input:-moz-placeholder{color:#fff;color:rgba(255,255,255,0.8)
}
.medium-editor-toolbar-form .medium-editor-toolbar-input::-moz-placeholder{color:#fff;color:rgba(255,255,255,0.8)
}
.medium-editor-toolbar-form .medium-editor-toolbar-input:-ms-input-placeholder{color:#fff;color:rgba(255,255,255,0.8)
}
.medium-editor-toolbar-form a{color:#fff
}
.medium-editor-toolbar-anchor-preview{background:#57ad68;color:#fff
}
.medium-editor-placeholder:after{color:#9ccea6
}
.default-view{height:100%;width:100%;margin-top:150px;margin-bottom:20px;padding:0 120px 0 40px;display:block;overflow-y:scroll;overflow-x:hidden
}
#signin-modal{margin:auto;position:absolute;top:0;bottom:0;left:0;right:0;width:400px;height:200px;background-color:white;box-shadow:0 0 100px #cdcdcd;padding:30px;z-index:99999;display:-moz-flex;display:-ms-flexbox;display:flex;-moz-flex-direction:column;-ms-flex-direction:column;flex-direction:column;-ms-flex-align:center;-moz-align-items:center;align-items:center;-ms-flex-pack:center;-moz-justify-content:center;justify-content:center
}
#signin-modal .close-btn{position:absolute;top:10px;right:10px;width:20px;height:20px;text-align:center;-webkit-touch-callout:none;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;cursor:pointer
}
#signin-modal .title{margin-bottom:10px;font-style:italic;color:#b98a5b;font-size:11px;line-height:17px
}
#signin-modal input{border:1px solid #c9c9c9;border-radius:2px;font-family:'Libre Baskerville', serif;font-size:13px;letter-spacing:1px;color:#515251;font-size:11px;padding:5px;margin:5px 0;width:100%
}
#signin-modal .submit-btn{width:100%;margin-top:5px;font-size:11px;padding:10px;background-color:#b98a5b;color:white;text-align:center;-webkit-touch-callout:none;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;cursor:pointer
}



