}
.medium-editor-toolbar-form .medium-editor-toolbar-input:focus,.medium-editor-toolbar-form label:focus{-webkit-appearance:none;-webkit-appearance--moz-appearance:none;-webkit-appearance--moz-appearance-appearance:none;border:none;box-shadow:none;outline:0
}
