}
.medium-editor-toolbar{background-color:#57ad68
}
.medium-editor-toolbar li{padding:0
}
.medium-editor-toolbar li button{min-width:60px;height:60px;border:none;border-right:1px solid #9ccea6;background-color:transparent;color:#fff;-webkit-transition:background-color 0.2s ease-in,color 0.2s ease-in;-webkit-transition-transition:background-color .2s ease-in, color .2s ease-in
}
