.default-view{height:100%;width:100%;margin-top:150px;margin-bottom:20px;padding:0 120px 0 40px;display:block;overflow-y:scroll;overflow-x:hidden
}
#sign-up{position:relative;background-color:#dedddd;margin:20px 20px 20px 0;-moz-flex:1 1 50%;-ms-flex:1 1 50%;flex:1 1 50%;display:-moz-flex;display:-ms-flexbox;display:flex;-ms-flex-pack:center;-moz-justify-content:center;justify-content:center;-ms-flex-align:center;-moz-align-items:center;align-items:center;overflow:hidden
}
#sign-up #center-panel{width:55%;height:78%;z-index:99;background-color:white;position:relative;padding:30px;font-family:'Anonymous Pro'
}
#sign-up #center-panel::after{content:'i';left:50%;font-style:italic;position:absolute;bottom:20px
}
#sign-up #center-panel .title{width:100%;text-align:center;font-family:'Anonymous Pro';padding:0 0 10px 0
}
#sign-up #center-panel .content p{margin-top:10px;line-height:22px
}
#sign-up #center-panel .links{margin-top:30px;width:100%;display:-moz-flex;display:-ms-flexbox;display:flex;-ms-flex-align:center;-moz-align-items:center;align-items:center;-ms-flex-pack:justify;-moz-justify-content:space-between;justify-content:space-between;border-top:1px solid #dedddd;border-bottom:1px solid #dedddd;padding:10px 0
}
#sign-up .back-pages{z-index:9;position:absolute;top:0;left:0;right:0;bottom:0;margin:auto;height:calc(78% - 4px);width:calc(55% - 4px);opacity:.5;background-color:white;border-right:1px solid #c0c0c0
}
#sign-up .back-pages:nth-last-of-type(8){height:81%;border:none;width:56%;box-shadow:-27px 20px 50px rgba(0,0,0,0.73)
}
.back-pages:nth-last-of-type(1){transform:translate3d(4px,0,0);-ms-transform:translate3d(4px,0,0)
}
.back-pages:nth-last-of-type(2){transform:translate3d(8px,0,0);-ms-transform:translate3d(8px,0,0)
}
.back-pages:nth-last-of-type(3){transform:translate3d(12px,0,0);-ms-transform:translate3d(12px,0,0)
}
.back-pages:nth-last-of-type(4){transform:translate3d(16px,0,0);-ms-transform:translate3d(16px,0,0)
}
.back-pages:nth-last-of-type(5){transform:translate3d(20px,0,0);-ms-transform:translate3d(20px,0,0)
}
.back-pages:nth-last-of-type(6){transform:translate3d(24px,0,0);-ms-transform:translate3d(24px,0,0)
}
.back-pages:nth-last-of-type(7){transform:translate3d(28px,0,0);-ms-transform:translate3d(28px,0,0)
}
.back-pages:nth-last-of-type(8){transform:translate3d(32px,0,0);-ms-transform:translate3d(32px,0,0)
}



